import Vue from 'vue'
import Router from 'vue-router'
import catalog from '@/views/catalog'
import pdf from '@/views/pdf'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: catalog
    },
    {
      path: '/pdf',
      name: 'pdf',
      redirect: {name: 'pdfDemo1'},
      component: pdf,
      children: [{
        path: 'pdfDemo1',
        name: 'pdfDemo1',
        component: () => import('@/views/pdf/pdfDemo1')
      }, {
        path: 'pdfDemo2',
        name: 'pdfDemo2',
        component: () => import('@/views/pdf/pdfDemo2')
      }, {
        path: 'pdfDemo3',
        name: 'pdfDemo3',
        component: () => import('@/views/pdf/pdfDemo3')
      }]
    }
  ]
})



