export function loadStyleByString(css, id) {
  var style = document.createElement("style")
  style.type = "text/css"
  style.id = id || "my_style"
  try {
    // IE会报错不允许向<style>元素添加子节点
    style.appendChild(document.createTextNode(css))
  } catch (ex) {
    // 此时,访问元素的StyleSheet属性,该属性有有一个cssText属性,可以接受CSS代码
    style.styleSheet.cssText = css
  }
  var head = document.getElementsByTagName("head")[0]
  var oldStyle = document.querySelector('#'+style.id)
  if(oldStyle) head.removeChild(oldStyle)
  head.appendChild(style)
}



