// 打印类属性、方法定义
/* eslint-disable */
const Print = function(dom, options) {
  if (!(this instanceof Print)) return new Print(dom, options);

  this.options = this.extend({
    'noPrint': '.no-print'
  }, options);

  if ((typeof dom) === "string") {
    this.dom = document.querySelector(dom);
  } else {
    this.isDOM(dom)
    this.dom = this.isDOM(dom) ? dom : dom.$el;
  }

  this.init();
};
Print.prototype = {
  init: function() {
    var content = this.getStyle() + this.getHtml()
    this.writeIframe(content);
  },
  extend: function(obj, obj2) {
    for (var k in obj2) {
      obj[k] = obj2[k];
    }
    return obj;
  },

  getStyle: function() {
    var str = "",
      styles = document.querySelectorAll('style,link');
    for (var i = 0; i < styles.length; i++) {
      str += styles[i].outerHTML;
    }
    str += "<style>" + (this.options.noPrint ? this.options.noPrint : '.no-print') + "{display:none;}</style>";
    // 去掉页眉页脚
    // str += "<style>@page{size: auto A4 landscape;margin: 3mm;}</style>"
    return str;
  },

  getHtml: function() {
    var inputs = document.querySelectorAll('input');
    var textareas = document.querySelectorAll('textarea');
    var selects = document.querySelectorAll('select');

    for (var k = 0; k < inputs.length; k++) {
      if (inputs[k].type == "checkbox" || inputs[k].type == "radio") {
        if (inputs[k].checked == true) {
          inputs[k].setAttribute('checked', "checked")
        } else {
          inputs[k].removeAttribute('checked')
        }
      } else if (inputs[k].type == "text") {
        inputs[k].setAttribute('value', inputs[k].value)
      } else {
        inputs[k].setAttribute('value', inputs[k].value)
      }
    }

    for (var k2 = 0; k2 < textareas.length; k2++) {
      if (textareas[k2].type == 'textarea') {
        textareas[k2].innerHTML = textareas[k2].value
      }
    }

    for (var k3 = 0; k3 < selects.length; k3++) {
      if (selects[k3].type == 'select-one') {
        var child = selects[k3].children;
        for (var i in child) {
          if (child[i].tagName == 'OPTION') {
            if (child[i].selected == true) {
              child[i].setAttribute('selected', "selected")
            } else {
              child[i].removeAttribute('selected')
            }
          }
        }
      }
    }
    return this.dom.innerHTML;
    //不使用outerHtml，使用innerHtml，方便打印时自定义页眉页脚
    //原因：1打印时对比body元素和其直接子元素之间的高度height
    //所以想要自适应的设置每一个page的高度为body（在这里指打印纸如A4）的高度
    //就把每一个page作为body的直接子元素，以body作为父容器，既可以实现分页，也设置了高度
    //最后每一个page设置为position：relative，page里的元素设置为absolute，方便自定义页眉页脚
    //原因：1打印时对比body元素和其直接子元素之间的高度height
    return this.dom.outerHTML;
    //下面的会产生bug，当要打印body所有的dom元素时，这时vue的路由会根据包括进来的js逻辑跳转到首页
    //这时打印会打印首页的内容，与预期不符,在本人的项目中不需要
    //下面的目的主要时为了防止根、父级别开头的 css 选择器不生效
    //而且我这边是要隐藏父元素，思路：pdf =》 canvas（预览） =》 img（隐藏） =》 iframe =》 print
    //（后续想办法优化，现在可能的思路是获取每个元素节点的当前样式getComputedStyle、currentStyle，保存到iframe中）
    // 包裹要打印的元素
    // fix: https://github.com/xyl66/vuePlugs_printjs/issues/36
    // return this.wrapperRefDom(this.dom).outerHTML;
  },
  // 向父级元素循环，包裹当前需要打印的元素
  // 防止根级别开头的 css 选择器不生效
  // wrapperRefDom: function(refDom) {
  //   let prevDom = null
  //   let currDom = refDom
  //   while (currDom && currDom.tagName.toLowerCase() !== 'body') {
  //     if (prevDom) {
  //       let element = currDom.cloneNode(false)
  //       element.appendChild(prevDom)
  //       prevDom = element
  //     } else {
  //       prevDom = currDom.cloneNode(true)
  //     }

  //     currDom = currDom.parentElement
  //   }

  //   return currDom.tagName.toLowerCase() === 'body' ? currDom : prevDom
  // },

  writeIframe: function(content) {
    var w, doc, iframe = document.createElement('iframe'),
      f = document.body.appendChild(iframe);
    iframe.id = "myIframe";
    //iframe.style = "position:absolute;width:0;height:0;top:-10px;left:-10px;";
    // iframe.setAttribute('style', 'position:absolute;width:0;height:0;top:-10px;left:-10px;');
    iframe.setAttribute('style', 'position:absolute;width:100%;height:300px;top:100px;left:10px; display: none;');
    w = f.contentWindow || f.contentDocument;
    doc = f.contentDocument || f.contentWindow.document;
    doc.open();
    doc.write(content);
    doc.title = 'cesh'
    doc.close();
    var _this = this
    iframe.onload = function() {
      _this.toPrint(w);
      setTimeout(function() {
        // document.body.removeChild(iframe)
      }, 100)
    }
  },

  toPrint: function(frameWindow) {
    try {
      setTimeout(function() {
        frameWindow.focus();
        try {
          if (!frameWindow.document.execCommand('print', false, null)) {
            frameWindow.print();
          }
        } catch (e) {
          frameWindow.print();
        }
        frameWindow.close();
      }, 10);
    } catch (err) {
      console.log('err', err);
    }
  },
  isDOM: (typeof HTMLElement === 'object') ?
    function(obj) {
      return obj instanceof HTMLElement;
    } : function(obj) {
      return obj && typeof obj === 'object' && obj.nodeType === 1 && typeof obj.nodeName === 'string';
    }
};
const MyPlugin = {}
MyPlugin.install = function(Vue, options) {
  // 4. 添加实例方法
  Vue.prototype.$print = Print
}
export default MyPlugin



