import Vue from 'vue'
import Vuex from 'vuex'

//挂载Vuex
Vue.use(Vuex)

//创建VueX对象
const store = new Vuex.Store({
  state: {
    //存放的键值对就是所要管理的状态
    name: 'helloVueX',
    options: {}
  },
  getters: {
    name: state => state.name,
    options: state => state.options,
  },
  mutations: {
    SET_OPTIONS(state, value) {
      state.options = value
    }
  },
  // actions: {
  //   SetOptions({commit}, params) {
  //     commit('SET_OPTIONS', params)
  //   }
  // }
})

export default store



