<template>
  <div class="pdf" style="text-align: center;">
    <el-button icon="el-icon-arrow-right" circle style="position: fixed; top: 50%; left: 0; z-index: 100;" @click="drawer=!drawer" plain></el-button>
    <el-drawer :visible.sync="drawer" :with-header="false" direction="ltr">
      <h2 style="margin-top: 100px;">pdf预览打印demo!</h2>
      <div>
        <el-button type="text" @click="$router.push({name: 'pdfDemo1'})">分页预览</el-button>
      </div>
      <div>
        <el-button type="text" @click="$router.push({name: 'pdfDemo2'})">多页预览</el-button>
      </div>
      <div>
        <el-button type="text" @click="$router.push({name: 'pdfDemo3'})">多pdf、多页预览</el-button>
      </div>
    </el-drawer>
    <div style="position: fixed; top: 0;width: 100%; z-index: 100; background: white; text-align: left;">
      <el-button-group style="min-width: 500px;">
        <el-progress :percentage="percentage"></el-progress>
        <el-button size="small" icon="el-icon-minus" @click="decrease(10)">10%</el-button>
        <el-button size="small" icon="el-icon-minus" @click="decrease(1)">1%</el-button>
        <el-button size="small" disabled>修改canvas大小</el-button>
        <el-button size="small" @click="dialogShow = true">更多设置</el-button>
        <el-button size="small" icon="el-icon-plus" @click="increase(1)">1%</el-button>
        <el-button size="small" icon="el-icon-plus" @click="increase(10)">10%</el-button>
      </el-button-group>
      <el-input @blur="loadStyle" type="textarea" :rows="2" placeholder="可以在这里输入适配的css样式,例如canvas{width: 100%!important;}" v-model="styleText" style="position: absolute; padding-left: 500px; width: 100%; box-sizing: border-box; left: 0;z-index: -1;"></el-input>
      <el-dialog title="更多配置" :visible.sync="dialogShow" width="90%">
        <div>
          <div class="option-item">
            <span>是否使用标题</span>
            <el-radio v-model="options.title.checked" :label="true">是</el-radio>
            <el-radio v-model="options.title.checked" :label="false">否</el-radio>
            <el-input v-show="options.title.checked" placeholder="请输入标题内容" v-model="options.title.value">
              <span slot="prepend">标题</span>
            </el-input>
            <el-input v-show="options.title.checked" type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="请输入样式" v-model="options.title.style" style="width: 100%;">
            </el-input>
          </div>
          <div class="option-item">
            <span>是否使用日期</span>
            <el-radio v-model="options.date.checked" :label="true">是</el-radio>
            <el-radio v-model="options.date.checked" :label="false">否</el-radio>
            <el-input v-show="options.date.checked" placeholder="请输入日期内容" v-model="options.date.value">
              <span slot="prepend">日期</span>
            </el-input>
            <el-input v-show="options.date.checked" type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="请输入样式" v-model="options.date.style" style="width: 100%;">
            </el-input>
          </div>
          <div class="option-item">
            <span>是否使用页数</span>
            <el-radio v-model="options.pageNo.checked" :label="true">是</el-radio>
            <el-radio v-model="options.pageNo.checked" :label="false">否</el-radio>
            <el-input :disabled="true" v-show="options.pageNo.checked" placeholder="请输入页数内容" v-model="options.pageNo.value">
              <span slot="prepend">页数</span>
            </el-input>
            <el-input v-show="options.pageNo.checked" type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="请输入样式" v-model="options.pageNo.style" style="width: 100%;">
            </el-input>
          </div>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogShow = false">取 消</el-button>
          <el-button type="primary" @click="setOption">确 定</el-button>
        </div>
      </el-dialog>
    </div>
    <router-view :style="'width: '+percentage+'%;margin: 50px auto;'" v-if="PDFShow"></router-view>
  </div>
</template>

<script>
  import { loadStyleByString } from '@/utils/util'
  import { mapMutations } from 'vuex'
  export default {
    provide() {
      return {
        reloadPDF: this.reloadPDF
      }
    },
    data() {
      return {
        PDFShow: true,
        drawer: false,
        percentage: 100,
        styleText: `
          canvas{
            width: 100%!important;
          }
          @page {
            margin: 5mm;
          }`,
        dialogShow: false,
        options: {
          title: { checked: true, value: '此处为自定义标题', style: `
            top: 0mm;
            left: 50%;
            width: 300px;
            margin-left: -150px;
            white-space: nowrap;
            text-align: center;` },
          date: { checked: true, value: (new Date()).toLocaleDateString(), style: `
            left: 0mm;
            top: 0mm;` },
          pageNo: { checked: true, value: 'page', style: `
            right: 0mm;
            bottom: 0mm;` },
        },
      }
    },
    created() {
      this.setOption()
    },
    methods: {
      ...mapMutations(['SET_OPTIONS']),
      loadStyle() {
        try {
          loadStyleByString(this.styleText)
          this.reloadPDF()
        } catch (err) {
          console.log(err)
        }
      },
      decrease(val) {
        this.percentage -= val
        if (this.percentage < 0) {
          this.percentage = 0
        }
        this.reloadPDF()
      },
      increase(val) {
        this.percentage += val
        if (this.percentage > 100) {
          this.percentage = 100
        }
        this.reloadPDF()
      },
      reloadPDF() {
        this.PDFShow = false
        this.$nextTick(function() {
          this.PDFShow = true
        })
      },
      setOption() {
        this.SET_OPTIONS(this.options)
        this.dialogShow = false
      }
    }
  }
</script>

<style scoped>
  /* .pdf {
    padding: 50px 20px;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    overflow: hidden;
  } */
</style>



